/* BROWSER RESET */
/*****************/
body {
  margin: 0; }

img {
  max-width: 100%;
  height: auto;
  border: 0; }

html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

/* BROWSER RESET END */
