.bg--extend {
  position: relative;
  /* Make nested column extends overlay each other correctly */ }
  .bg--extend:before, .bg--extend:after {
    content: "";
    position: absolute;
    top: 0;
    width: 1400px;
    height: 100%;
    background: inherit; }
  .bg--extend:before {
    right: 100%; }
  .bg--extend:after {
    left: 100%; }
  .bg--extend-left, .bg--extend-right {
    position: relative; }
  .bg--extend-left {
    background-position: left top; }
    .bg--extend-left:before {
      content: "";
      position: absolute;
      top: 0;
      width: 1400px;
      height: 100%;
      background: inherit;
      right: 100%;
      background-position: right top; }
  .bg--extend-right {
    background-position: right top; }
    .bg--extend-right:after {
      content: "";
      position: absolute;
      top: 0;
      width: 1400px;
      height: 100%;
      background: inherit;
      left: 100%;
      background-position: left top; }
  .bg--extend .bg--extend {
    z-index: 1; }
    .bg--extend .bg--extend-left, .bg--extend .bg--extend-right {
      z-index: 1; }

